* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

:root {
  --very-dark-magenta: hsl(300, 43%, 22%);
  --soft-pink: hsl(333, 80%, 67%);
  --dark-grayish-magenta: hsl(303, 10%, 53%);
  --light-grayish-magenta: hsl(300, 24%, 96%);
  --white: hsl(0, 0%, 100%); }

html {
  box-sizing: border-box;
  font-size: 62.5%; }

body {
  font-family: 'League Spartan', sans-serif;
  background-image: url("../images/bg-pattern-top-desktop.svg"), url("../images/bg-pattern-bottom-desktop.svg");
  background-position: left top, right bottom;
  background-repeat: no-repeat;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative; }

.container {
  max-width: 100rem;
  /* height: 20rem; */
  /* background-color: red; */
  box-shadow: 0 3rem 15rem rgba(0, 0, 0, 0.181);
  padding: 6rem 10rem;
  margin-bottom: 3rem; }
  .container .social {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 3rem; }
    .container .social__intro {
      display: flex;
      flex-direction: column;
      gap: 2rem;
      align-items: flex-start;
      padding-right: 14rem; }
      .container .social__intro--title {
        font-size: 4rem; }
      .container .social__intro--description {
        font-size: 1.5rem;
        color: var(--soft-pink); }
    .container .social__ratings {
      background-color: blueviolet;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 3rem; }
      .container .social__ratings--rate {
        width: 70%;
        background-color: red;
        padding: 1.2rem;
        border-radius: 5px; }
  .container .reviews {
    display: grid;
    grid-template-columns: repeat(3, 1fr); }

.footer {
  position: absolute;
  background-color: blueviolet;
  width: 100%;
  height: 3rem;
  top: calc(100vh - 3rem); }
